@font-face {
  font-family: "Volvo Novum";
  src: url("../../fonts/volvo-novum-light.woff2") format("woff2"), url("../../fonts/volvo-novum-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Volvo Novum";
  src: url("../../fonts/volvo-novum-regular.woff2") format("woff2"), url("../../fonts/volvo-novum-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Volvo Novum";
  src: url("../../fonts/volvo-novum-medium.woff2") format("woff2"), url("../../fonts/volvo-novum-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Volvo Novum";
  src: url("../../fonts/volvo-novum-bold.woff2") format("woff2"), url("../../fonts/volvo-novum-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Volvo Novum", "Arial", sans-serif;
  color: #0A0A0A;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

button {
  cursor: pointer;
}

.title-lowcase {
  font-size: 32px;
  font-weight: 500;
  line-height: 38px;
  text-align: center;
  margin-top: 22px;
  margin-bottom: 22px;
}
@media (min-width: 1440px) {
  .title-lowcase {
    font-size: 48px;
    font-weight: 500;
    line-height: 58px;
    text-align: center;
    margin-top: 48px;
    margin-bottom: 48px;
  }
}

.transparent-button {
  background-color: transparent;
  color: #2A609D;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  width: 100%;
  padding: 11px 10px;
  border: 1px solid #2A609D;
  border-radius: 4px;
  display: inline-block;
  margin: 23px auto 40px;
}
@media (min-width: 768px) {
  .transparent-button {
    width: max-content;
    margin-left: 0;
  }
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 16px;
  position: relative;
}
@media (min-width: 1440px) {
  .container {
    padding: 0 32px;
  }
}
@media (min-width: 1920px) {
  .container {
    padding: 0 100px;
  }
}

[data-button=open] {
  border: none;
  background-color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  color: #0A0A0A;
  padding-right: 0;
}

[data-button=close] {
  background-color: #ffffff;
  border: none;
  width: 24px;
  height: 24px;
  position: relative;
}
[data-button=close] svg {
  position: absolute;
  top: 0;
  left: 0;
  fill: #0A0A0A;
}

.main-header__base {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 64px;
}
.main-header__base svg {
  margin-bottom: 2px;
}

.main-header__navigation-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 73px;
  padding: 16px;
  position: relative;
}
.main-header__navigation-button svg {
  margin-bottom: 3px;
}
@media (min-width: 1440px) {
  .main-header__navigation-button {
    padding: 16px 24px;
    min-height: 73px;
  }
}
.main-header__navigation-button::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #E0E0E0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.main-header__navigation-list,
.main-header__navigation-social-list {
  padding: 16px;
}
@media (min-width: 1440px) {
  .main-header__navigation-list,
.main-header__navigation-social-list {
    padding: 16px 24px 24px;
  }
}

.main-header__navigation-list {
  position: relative;
}
.main-header__navigation-list::after {
  content: "";
  height: 1px;
  background-color: #E0E0E0;
  position: absolute;
  left: 16px;
  right: 16px;
  bottom: 0;
}
@media (min-width: 1440px) {
  .main-header__navigation-list::after {
    left: 24px;
    right: 24px;
  }
}
.main-header__navigation-list li {
  padding: 8px 0;
}
@media (min-width: 1440px) {
  .main-header__navigation-list li {
    padding: 12px 0;
  }
}
.main-header__navigation-list li a {
  width: 100%;
  display: block;
}

.main-header__navigation-social-list {
  display: flex;
  align-items: center;
  gap: 20px;
}

.dark {
  position: relative;
}
.dark::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6980392157);
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

#sidebar {
  position: fixed;
  top: 0;
  width: 100%;
  height: auto;
  padding-bottom: 25px;
  background-color: #ffffff;
  right: -100%;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 1;
}
@media (min-width: 768px) {
  #sidebar {
    max-width: 400px;
    height: 100%;
    padding-bottom: 0;
    font-size: 20px;
    font-weight: 500;
    line-height: 28px;
  }
}

#sidebar.active {
  right: 0;
}

.hero {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 0;
}
.hero::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1019607843);
}
.hero img {
  object-fit: cover;
  object-position: center 60%;
  width: 100%;
  height: 397px;
}
@media (min-width: 1440px) {
  .hero img {
    height: 584px;
  }
}

.hero .container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.hero__wrapper,
.hero__text-wrapper {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
}
@media (min-width: 1440px) {
  .hero__wrapper,
.hero__text-wrapper {
    top: 25px;
  }
}
.hero__wrapper h2,
.hero__text-wrapper h2 {
  font-size: 32px;
  font-weight: 500;
  line-height: 38px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  max-width: 820px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 1440px) {
  .hero__wrapper h2,
.hero__text-wrapper h2 {
    font-size: 48px;
    line-height: 58px;
  }
}
.hero__wrapper p,
.hero__text-wrapper p {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 24px;
  margin-bottom: 0;
}
@media (min-width: 1440px) {
  .hero__wrapper p,
.hero__text-wrapper p {
    font-size: 20px;
    line-height: 28px;
  }
}
.hero__wrapper a,
.hero__text-wrapper a {
  display: block;
  width: 224px;
  background-color: #ffffff;
  padding: 12px 32px;
  text-align: center;
  border-radius: 4px;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

@media (min-width: 1440px) {
  .hero__text-wrapper {
    top: 40px;
  }
}
.hero__text-wrapper h2 {
  margin-top: 8px;
  margin-bottom: 18px;
  padding: 0 10px;
}

.message {
  width: 100%;
  background-color: #0A0A0A;
  color: #ffffff;
  opacity: 1;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.message__wrapper {
  position: relative;
  padding: 12px 24px 10px;
}
.message__wrapper p {
  margin: 0;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
}
@media (min-width: 1440px) {
  .message__wrapper {
    padding: 16px 24px 13px;
  }
}

[data-button=close-message] {
  background-color: #0A0A0A;
  border: none;
  width: 24px;
  height: 24px;
  position: absolute;
  right: 0;
  top: 11px;
}
[data-button=close-message] svg {
  position: absolute;
  top: 0;
  left: 0;
  fill: #ffffff;
}

.links__list {
  padding: 41px 0 64px;
}
@media (min-width: 768px) {
  .links__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
    padding: 72px 0;
  }
}
@media (min-width: 1440px) {
  .links__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 24px;
    padding: 72px 0;
  }
}
.links__list li p {
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  height: max-content;
  margin: 24px 0 24px;
}
@media (min-width: 1440px) {
  .links__list li p {
    margin-bottom: 32px;
  }
}
.links__list li a {
  color: #2A609D;
  font-size: 16px;
  font-weight: 500;
  margin-top: 22px;
  display: block;
}
.links__list li a svg {
  fill: #2A609D;
  margin-left: 3px;
}
@media (min-width: 1440px) {
  .links__list li a {
    margin-top: 32px;
  }
}
.links__list li img {
  width: 100%;
  max-height: 350px;
  object-fit: cover;
  object-position: 30% 30%;
}

.links-add {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
@media (min-width: 768px) {
  .links-add {
    flex-direction: row;
    gap: 40px;
  }
}
.links-add li a {
  color: #2A609D;
  font-size: 16px;
  font-weight: 500;
  margin-top: 22px;
  display: block;
  margin: 10px 0;
}
.links-add li a svg {
  fill: #2A609D;
  margin-left: 3px;
}
@media (min-width: 1440px) {
  .links-add li a {
    margin-top: 32px;
  }
}

a[disabled] {
  color: #0A0A0A !important;
}

.text-block {
  padding: 30px 0;
}

.links-account {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 15px;
  margin-top: 24px;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .links-account {
    margin-top: 30px;
    margin-bottom: 50px;
    flex-direction: row;
    gap: 25px;
  }
}
.links-account li {
  width: 100%;
  background-color: #0A0A0A;
  border-radius: 4px;
  padding: 12px 32px;
}
@media (min-width: 768px) {
  .links-account li {
    width: max-content;
  }
}
.links-account a {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  color: #ffffff;
  display: block;
}

.text-block {
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  text-align: center;
  color: #5C5C5C;
}

.contacts {
  margin-top: 23px;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .contacts {
    margin-top: 48px;
    margin-bottom: 68px;
  }
}

.contacts__list {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
@media (min-width: 768px) {
  .contacts__list {
    gap: 26px;
    justify-content: center;
    flex-direction: row;
    flex-grow: 1;
    flex-basis: 0;
    flex-wrap: wrap;
  }
}
.contacts__list li {
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .contacts__list li {
    width: 40%;
  }
}
@media (min-width: 1440px) {
  .contacts__list li {
    width: 32%;
  }
}
.contacts__list li h3 {
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  text-align: center;
  margin-top: 13px;
  margin-bottom: 9px;
}
@media (min-width: 768px) {
  .contacts__list li h3 {
    margin-top: 10px;
  }
}
.contacts__list li .contacts__links div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 13px;
}
.contacts__list li .contacts__links div a {
  color: #2A609D;
  font-family: "Volvo Novum", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  display: inline-block;
  margin: 0 7px;
}
.contacts__list li .contacts__links div a svg {
  fill: #2A609D;
  margin-left: 3px;
  margin-top: 6px;
